.button-link {
  color: #fff;
  transition: color var(--animation-duration) var(--timing-function), background-color var(--animation-duration) var(--timing-function);
  background-color: #0a0a23;
  border: none;
  border-radius: 10px;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none;
  box-shadow: 0 0 2px 2px #000;
}

.buttonBox li {
  margin-bottom: 10px;
}

.button-link:hover, .button-link:focus {
  color: #0a0a23;
  background-color: #fff;
}

.btn-box_color_switcher {
  justify-content: center;
  margin-top: 150px;
  display: flex;
}

.btn-box_color_switcher button {
  margin-right: 50px;
}

.btn-color_switcher {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  min-height: 3.75em;
  min-width: 0;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  will-change: transform;
  background-color: #fff;
  border: .125em solid #1a1a1a;
  border-radius: .9375em;
  outline: none;
  margin: 0;
  padding: 1em 2.3em;
  font-family: Roobert, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  text-decoration: none;
  transition: all .3s cubic-bezier(.23, 1, .32, 1);
  display: inline-block;
}

.btn-color_switcher:disabled {
  pointer-events: none;
}

.btn-color_switcher:hover {
  color: #fff;
  background-color: #1a1a1a;
  transform: translateY(-2px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, .25);
}

.btn-color_switcher:active {
  box-shadow: none;
  transform: translateY(0);
}

.btn-color_switcher:disabled {
  background-color: rgba(0, 0, 0, 0);
}

